import { Card } from 'types/Card';

interface MockSaveCardInfoResponse {
  success: boolean;
  data: Card;
  error: string;
}

export const mockSaveCardInfo = async (
  cardInfo: Card
): Promise<MockSaveCardInfoResponse> => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({
        success: true,
        data: { ...cardInfo },
        error: '  .',
      });
    }, 2500);
  });
};
